<template>
    <div-row :obj="obj" class="content-wrapper text-picture">
        <el-row :gutter="obj.gutter">
            <el-col>
                <h1 class="title" v-html="obj.title" :class="obj.animateTitle? 'wow '+obj.animateTitle : ''"></h1>
                <p class="desc" v-html="obj.desc" :class="obj.animateText? 'wow '+obj.animateText : ''" ></p>
            </el-col>
            <el-col :span="24/obj.columns" v-for="(item,index) in obj.imgTextBox" :key="index">
                <div v-if="obj.imgPosition=='left' || obj.imgPosition=='right' " :class="obj.imgPosition=='left'? 'textImgLeftBox': 'textImgRightBox'">
                    <div class="clearfix" @click="goClick(item.httpUrl)">
                        <div class="img image" :style="{width: obj.imgWidth+'%'}" :class="obj.imgPosition=='left'? 'f-left':'f-right'">
                            <div :class="obj.animateImg? 'wow '+obj.animateImg : ''">
                                <img :src="item.imgUrl">
                            </div>
                        </div>
                        <div class="text-box" :style="{width: (100-obj.imgWidth)+'%'}" :class="obj.imgPosition=='left'? 'f-right': 'f-left'">
                            <div class="textBox" :class="obj.animateItemText? 'wow '+obj.animateItemText : ''">
                                <h4 class="text-title text-center" v-html="item.title"></h4>
                                <h5 class="text-desc text-center" v-html="item.desc"></h5>
                                <p class="text-info" v-html="item.info"></p>  
                            </div>                     
                        </div>
                    </div>
                </div>
                <div v-if="obj.imgPosition=='top'" class="textImgTopBox" @click="goClick(item.httpUrl)">
                    <div class="img image" :style="{width: obj.imgWidth+'%'}">
                        <div :class="obj.animateImg? 'wow '+obj.animateImg : ''">
                            <img :src="item.imgUrl">
                        </div>
                    </div>
                    <div class="text-box">
                        <div class="textBox" :class="obj.animateItemText? 'wow '+obj.animateItemText : ''">
                            <h4 class="text-title text-center" v-html="item.title"></h4>
                            <h5 class="text-desc text-center" v-html="item.desc"></h5>
                            <p class="text-info" v-html="item.info"></p>  
                        </div>                     
                    </div>
                </div>
                <div v-if="obj.imgPosition=='bottom'" class="textImgBottomBox" @click="goClick(item.httpUrl)">
                    <div class="text-box">
                        <div class="textBox" :class="obj.animateItemText? 'wow '+obj.animateItemText : ''">
                            <h4 class="text-title text-center" v-html="item.title"></h4>
                            <h5 class="text-desc text-center" v-html="item.desc"></h5>
                            <p class="text-info" v-html="item.info"></p>  
                        </div>                     
                    </div>
                    <div class="img image" :style="{width: obj.imgWidth+'%'}">
                        <div :class="obj.animateImg? 'wow '+obj.animateImg : ''">
                            <img :src="item.imgUrl" :style="{height:obj.imgHeight+'px'}">
                        </div>
                    </div>
                </div>
            </el-col>
        </el-row>     
    </div-row>
</template>
<script>
import animate from '@/mixins/animate'
export default {
    name: 'TwoColumns',
    mixins: [animate]
    
}
</script>
<style lang="less" scoped>
    .content-wrapper{
        .title,.desc{
            text-align: center
        }
        .title{
            font-size: 40px;
            color:#666;
            padding: 10px 0;
        }
        .desc{
            color:#bdc3c7;
            font-size: 16px;
        }
        .image{
            margin: 0 auto;
            width: 100%;
            padding: 20px;
            display: flex;
            justify-content: center;
            align-items: center;  
            img{
                max-width: 100%;
                height: auto;
                display: block;
            }         
        }
        
        .textImgLeftBox,.textImgRightBox{
            position: relative;
            .image{
                width: 50%;
            }
            .text-box{
                width: 50%;
                .textBox{
                    position:absolute;
                    width: 50%;
                    right: 0;
                    top: 50%;
                    transform: translate(0,-50%);
                    padding: 10px;
                    box-sizing: border-box;
                }
            }
        }
        .textImgLeftBox{
            .text-box{
                .textBox{
                   right: 0
                }
            }
        }
        .textImgRightBox{
            .text-box{
                .textBox{
                    left: 0
                }
            }
        }

        .text-box{
            .text-title,.text-desc{
                text-align: left;
                overflow: hidden;
                text-overflow:ellipsis;
            }
            .text-title{
                white-space: nowrap;
            }
            .text-desc{
                display: -webkit-box;
                -webkit-line-clamp: 5;
                -webkit-box-orient: vertical;
                line-height: 20px;
                min-height: 60px;
                font-size: 13px;
            }
        }
    }
</style>



